<template>
<div>
    <div>这是其他</div> 
    <div @click="enterHome()">路由跳转</div> 
    <div @click="setData()">存VUEX</div>
    <div @click="getData()">取vuex</div>
    <p>{{name}}</p>
    <input type="text" v-model="name">
    <div>
        <keep-alive >
            <!-- 子路由出口 -->
            <router-view></router-view>
        </keep-alive>
    </div>
    
</div>

</template>
<script>
export default {
    data(){
        return{
            name:'张三'
        }
    },
    methods:{
        enterHome(){
            this.comeRouter('/test');
        },
        setData(){
            this.comeRouter('/set');
        },
        getData(){
            this.comeRouter('/get');
        }
    }
}
</script>



